<template>
  <div id="edit-blog">
    <h2>添加博客</h2>
    <form v-if="!submmited">
        <label>博客标题</label>
        <input type="text" v-model="blog.title" required />

        <label>博客内容</label>
        <textarea v-model="blog.content"></textarea>

        <div id="checkboxes">
            <label>分类：</label>
            <input type="checkbox" value="随笔" v-model="blog.categories">
            <label>随笔</label>
            <input type="checkbox" value="文章" v-model="blog.categories">
            <label>文章</label>
            <input type="checkbox" value="杂谈" v-model="blog.categories">
            <label>杂谈</label>
        </div>
        <label>作者：</label>
        <select v-model="blog.author">
            <option v-for="author in authors" :key="author">
                {{author}}
            </option>
        </select>
        <button v-on:click.prevent="post">编辑博客</button>
    </form>

    <div>
        <h3 v-if="submmited">您的博客编辑成功</h3>
    </div>

    <div id="preview">
        <h3>博客总览</h3>
        <p>博客标题：{{blog.title}}</p>
        <p>博客内容：</p>
        <p>{{blog.content}}</p>
        <p>博客分类：</p>
        <ul>
            <li v-for="category in blog.categories" :key="category.value">
                {{category}}
            </li>
        </ul>
        <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'edit-blog',
  data () {
    return {
        id:this.$route.params.id,
        blog:{},
        authors:["小怪","胡图图","牛爷爷","小美","刷子"],
        submmited:false
    }
  },
  methods:{
      fetchData()
      {
          //console.log(this.id);
          this.$http.get('https://vuedemo-b1233.firebaseio.com/posts/' + this.id + ".json")
          .then(Response =>{
              //console.log(Response.body);
              this.blog = Response.body;
          })
      },
      post:function(){
          this.$http.put('https://vuedemo-b1233.firebaseio.com/posts/' + this.id + ".json",this.blog)
          .then(function(data) {
              console.log(data);
              this.submmited = true;
          });
      }
  },
  created(){
      this.fetchData();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#edit-blog *{
    box-sizing: border-box;
}
#edit-blog{
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
    background: #CCCCCC;
}
label{
    display: block;
    margin: 20px 0 10px;
}
input[type="text"],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
}
textarea{
    height: 200px;
}

#checkboxes label{
    display: inline-block;
    margin-top: 0;
}
#checkboxes label{
    display: inline-block;
    margin-right: 10px;
}
button{
    display: block;
    margin: 20px 0;
    background: #333333;
    color:#fff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    cursor: pointer;
}
#preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
}
#h3
{
    margin-top: 10px;
}
</style>



