<template>
    <div>
        <ul>
            <li>
                <router-link to="/" exact>首页</router-link>
                <router-link to="/add" exact>写博客</router-link>
                <router-link to="/HelloWorld" exact>关于</router-link>
                <router-link to="#" exact>留言</router-link> 
            </li>
        </ul>
    </div>
</template>

<script>
    export default{
        name:"blog-header"
    }
</script>

<style scoped>
.logo{
    background: #fff;
}
ul{
    list-style-type: none;
    text-align: center;
    margin: 0;
}
li{
    display: inline-block;
    margin: 0 10px;
}

a{
    color: #fff;
    text-decoration: none;
    border-radius: 15px;
    padding: 15px;
}
div{
    background: #333333;
    padding: 30px 0;
    margin-bottom: 15px;
}
.router-link-active{
    background: rgba(255,255,255,0.8);
    color: #444;
}
</style>



